.flat
{
    -ms-flex-preferred-size:calc(50% - 20px);
        flex-basis:calc(50% - 20px);
    margin:10px;
    cursor: pointer;
    background-color:yellow;
}

.flat:hover
{
    opacity: 0.9;
}

.flat-picture
{
    height:200px;
    background-size:cover;
    background-position:center;
}

.flat-title
{
    margin:10px;
}


